<template>
  <section class="repay">
    <!-- title -->
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar class="mr-1" size="40" variant="light-danger">
          <img :src="require('@/assets/icons/building_icon.svg')" alt="" draggable="false" width="30" />
        </b-avatar>
        <h4 class="m-0">عودت وجه</h4>
      </div>
      <span> لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است 26</span>
    </div>

    <!-- main -->
    <main>
      <b-card class="mb-5">
        <b-form v-if="getUserPrepaidDetails">
          <b-form-group label-for="name" label="نام شخص گیرنده">
            <b-form-input id="name" type="text" readonly v-model="getUserPrepaidDetails.full_name" />
          </b-form-group>

          <b-form-group label-for="repay-money" label="مبلغ عودت " class="position-relative repay-money">
            <b-form-input dir="ltr" id="repay-money" readonly type="text" class="px-4" v-model="getUserPrepaidDetails.credit" />
          </b-form-group>

          <b-form-group label=" تاریخ عودت " class="repay-date">
            <b-form-row>
              <b-col>
                <v-select :searchable="false" :clearable="false" id="payment-method" maxHeight="100" v-model="selectedDay" :options="days" />
              </b-col>
              <b-col>
                <v-select :searchable="false" :clearable="false" id="payment-method" maxHeight="100" v-model="selectedMonth" :options="months" label="text" />
              </b-col>
              <b-col>
                <v-select :searchable="false" :clearable="false" id="payment-method" maxHeight="100" v-model="selectedYear" :options="years" />
              </b-col>
            </b-form-row>
          </b-form-group>

          <b-form-group label-for="payment-method" label=" نحوه پرداخت" class="payment-method">
            <v-select id="payment-method" v-model="paymentMethod" :options="paymentMethodOption" :searchable="false" label="text" />
          </b-form-group>
        </b-form>

        <div class="mt-5">
          <b-button block variant="primary" @click="store">
            <img :src="require('@/assets/icons/add_icon.svg')" alt="add" draggable="false" width="24" class="add-icon" />
            ثبت عودت وجه
          </b-button>
        </div>
      </b-card>
    </main>
  </section>
</template>

<script>
import AddIcon from "@core/components/svg-icons/AddIcon.vue";
import vSelect from "vue-select";
import {
  BAvatar,
  BButton,
  BCard,
  BFormGroup,
  BForm,
  BFormInput,
  BFormRow,
  BTable,
  BCol,
} from "bootstrap-vue";
import { getReceiptType, creditRefund } from '@/api/costsServices';
import { getTime } from '@/api/generalServices';
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue"

export default {
  name: 'PrepaidRepay',
  components: {
    BAvatar,
    BTable,
    BButton,
    BCard,
    BFormGroup,
    BForm,
    BFormInput,
    BFormRow,
    vSelect,
    AddIcon,
    BCol,
  },
  data() {
    return {
      years: [],
      months: (() => { return [...[...Array(12).keys()].map(i => i + 1)].map(k => k < 10 ? `0${k}` : k) })(),
      days: (() => { return [...[...Array(31).keys()].map(i => i + 1)].map(k => k < 10 ? `0${k}` : k) })(),
      paymentMethod: null,
      selectedDay: null,
      selectedMonth: null,
      selectedYear: null,
      paymentMethodOption: [],
    };
  },
  computed: {
    getUserPrepaidDetails() {
      return this.$store.state.costs.getUserPrepaidDetails;
    },
  },
  methods: {
    /** getdata multi select Receipt Type */
    async getReceiptType() {
      try {
        let req = await getReceiptType()
        this.paymentMethodOption = Object.entries(req.data.list).map((item) => {
          return {
            text: item[1],
            value: item[0]
          }
        });
      } catch (error) {
        console.error(error);
      }
    },

    async store() {
      /**validate */
      if (!this.selectedDay) return this.toast("روز الزامی است")
      if (!this.selectedMonth) return this.toast("ماه الزامی است")
      if (!this.selectedYear) return this.toast("سال الزامی است")
      if (!this.paymentMethod) return this.toast("نحوه پرداخت الزامی است")

      /**store */
      try {
        const { data } = await creditRefund({
          user_id: this.$route.params.id,
          refund_fee: this.getUserPrepaidDetails.credit,
          refund_date: `${this.selectedYear}/${this.selectedMonth}/${this.selectedDay}`,
          receipt_type_id: this.paymentMethod.value
        })

        this.toast(data.message, "success", "CheckSquareIcon")
        /**redirect to repay */
        this.$router.push({ name: "prepaid" })
      } catch (error) {
        console.error(error)
      }
    },

    /**
     * less code
     * @param {String} title 
     * @param {String} variant 
     * @param {String} icon 
     */
    toast(title, variant = "danger", icon = "AlertCircleIcon") {
      return this.$toast({
        component: ToastificationContent,
        props: {
          title: title,
          icon: icon,
          variant: variant,
        },
      })
    },

    /**get server time */
    async getTime() {
      try {
        const { data } = await getTime()
        const dt = String(data.locale_date).split('/')

        // dafault day 
        this.selectedDay = dt[2]

        /**get jalali years 4 years ahead and 4 years behind */
        this.years = (() => {
          const currentYear = parseInt(dt[0]);
          const yearsAhead = 4;
          const yearsBehind = 4;

          const years = [];
          for (let i = -yearsBehind; i <= yearsAhead; i++) {
            years.push(currentYear + i);
          }
          return years
        })()

        /**default month */
        this.selectedMonth = dt[1]

        /**default year */
        this.selectedYear = dt[0]
      } catch (error) {
        console.error(error)
      }
    }
  },
  async mounted() {
    this.getTime()
    this.getReceiptType();
  },
};
</script>

